}

.intro {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.text-group {
  display: inline-block;
  text-align: left;
  padding: 60 0 150 0;
}

.vertical-center {
  min-height: 100%;  /* Fallback for browsers do NOT support vh unit */
  min-height: 100vh;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.divider {
  width: 5px;
  height: auto;
  display: inline-block;
}

hr.hr-gradient {
  margin-top: 8px;
  margin-left: 0px;
  width: 69%;
  border-radius: 15px;
  height: 5px;
  border: 0;
  background-image: -webkit-gradient(linear, left top, right top, from(#F4B447), color-stop(#EE4C7A), color-stop(#00B199), to(#8CC66A));
  background-image: -webkit-linear-gradient(left, #F4B447, #EE4C7A, #00B199, #8CC66A);
  background-image: -o-linear-gradient(left, #F4B447, #EE4C7A, #00B199, #8CC66A);
  background-image: linear-gradient(90deg, #F4B447, #EE4C7A, #00B199, #8CC66A);
}

#github {
  background: -webkit-gradient(linear, left top, right top, from(#A166AB), color-stop(40%, #4E71B7), color-stop(60%, #0B97AC), to(#00B199));
  background: -webkit-linear-gradient(left, #A166AB 0%, #4E71B7 40%, #0B97AC 60%, #00B199 100%);
  background: -o-linear-gradient(left, #A166AB 0%, #4E71B7 40%, #0B97AC 60%, #00B199 100%);
  background: linear-gradient(to right, #A166AB 0%, #4E71B7 40%, #0B97AC 60%, #00B199 100%);
}

#resume {
  background: -webkit-gradient(linear, left top, right top, from(#E67207), color-stop(50%, #EF4E7B), to(#A166AB));
  background: -webkit-linear-gradient(left, #E67207 0%, #EF4E7B 50%, #A166AB 100%);
  background: -o-linear-gradient(left, #E67207 0%, #EF4E7B 50%, #A166AB 100%);
  background: linear-gradient(to right, #E67207 0%, #EF4E7B 50%, #A166AB 100%);
}

#email {
  background: -webkit-gradient(linear, left top, right top, from(#00B199), color-stop(50%, #6CB980), color-stop(85%, #8CC66A));
  background: -webkit-linear-gradient(left, #00B199 0%, #6CB980 50%, #8CC66A 85%);
  background: -o-linear-gradient(left, #00B199 0%, #6CB980 50%, #8CC66A 85%);
  background: linear-gradient(to right, #00B199 0%, #6CB980 50%, #8CC66A 85%)
}

img.hoverUp:hover {
  cursor: pointer;
  -webkit-transform: translate3d(0, -3px, 0);
          transform: translate3d(0, -3px, 0);
}

button {
  border: 0;
  position: relative;
  display: inline-block;
  width: auto;
  height: 48px;
  min-width: 160px;
  z-index: 1;
  padding: 9px 20px;
  font-weight: 700;
  letter-spacing: 3px;
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  border-radius: 4px;
  overflow: hidden;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition: all .4s ease-out;
  -o-transition: all .4s ease-out;
  transition: all .4s ease-out;
}

button:before {
  content: "";
  width: calc(100% - 4px);
  height: calc(100% - 4px);
  background: #22292C;
  position: absolute;
  top: 2px;
  left: 2px;
  border-radius: 2px;
  z-index: -1;
}

button:hover:before {
  -webkit-transition: all .4s ease-out;
  -o-transition: all .4s ease-out;
  transition: all .4s ease-out;
  opacity: 0;
}

button:hover {
  cursor: pointer;
}

button:focus {
  outline:0;
}

.arrows {
	width: 60px;
	height: 72px;
	position: absolute;
	left: 50%;
	margin-left: -30px;
	bottom: 20px;
}

.arrows:hover {
  cursor: pointer;
}

.arrows path {
	stroke: #2994D1;
	fill: transparent;
	stroke-width: 1px;
	animation: arrow 2s infinite;
	-webkit-animation: arrow 2s infinite;
}

.arrows path.a1 {
	animation-delay:-1s;
	-webkit-animation-delay:-1s; /* Safari 和 Chrome */
}

.arrows path.a2 {
	animation-delay:-0.5s;
	-webkit-animation-delay:-0.5s; /* Safari 和 Chrome */
}

.arrows path.a3 {
	animation-delay:0s;
	-webkit-animation-delay:0s; /* Safari 和 Chrome */
}

.star {
  display:block;
  width:4px;
  height:4px;
  border-radius:50%;
  background:#FFF;
  position:relative;
  -webkit-transform-origin:100% 0;
      -ms-transform-origin:100% 0;
          transform-origin:100% 0;
  -webkit-animation:star-ani 9s infinite ease-out;
          animation:star-ani 9s infinite ease-out;
  -webkit-animation-direction: normal;
          animation-direction: normal;
  opacity:0;
}

.star:after {
  content:'';
  position:absolute;
  top:0px;
  left:4px;
  border:2px solid #F00;
  border-width:2px 150px 2px 150px;
  border-color:transparent transparent transparent #FFF;
  -webkit-transform:rotate(-45deg) translate3d(1px, -1px, 0);
          transform:rotate(-45deg) translate3d(1px, -1px, 0);
  -webkit-transform-origin:0% 100%;
      -ms-transform-origin:0% 100%;
          transform-origin:0% 100%;
  -webkit-animation:shooting-ani 9s infinite ease-out;
          animation:shooting-ani 9s infinite ease-out;
  -webkit-animation-direction: normal;
          animation-direction: normal;
}

.pink {
  background:#EE4C7A;
  -webkit-animation-delay:0s;
          animation-delay:0s;
}

.pink:after {
  border-color:transparent transparent transparent #EE4C7A ;
  -webkit-animation-delay:0s;
          animation-delay:0s;
}

.blue {
  background:#0B97AC ;
  -webkit-animation-delay:3s;
          animation-delay:3s;
}

.blue:after {
  border-color:transparent transparent transparent #0B97AC;
  -webkit-animation-delay:3s;
          animation-delay:3s;
}

.yellow {
  background:#F69233;
  -webkit-animation-delay:6s;
          animation-delay:6s;
}

.yellow:after {
  border-color:transparent transparent transparent #F69233;
  -webkit-animation-delay:6s;
          animation-delay:6s;
}

.green {
  background: #6CB980;
  -webkit-animation-delay: 8s;
          animation-delay: 8s;
}

.green:after {
  border-color:transparent transparent transparent #6CB980;
  -webkit-animation-delay: 8s;
          animation-delay: 8s;
}

@keyframes arrow {
  0% {opacity:0}
  40% {opacity:1}
  80% {opacity:0}
  100% {opacity:0}
}

@-webkit-keyframes arrow {
  0% {opacity:0}
  40% {opacity:1}
  80% {opacity:0}
  100% {opacity:0}
}

@-webkit-keyframes shine {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 1;
  }
}

@keyframes shine {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 1;
  }
}

